import React from 'react'
import styled from 'styled-components'
import PancakeRoundIcon from './TokenLogo.png'
import Text from '../Text/Text'
import Skeleton from '../Skeleton/Skeleton'
import { Colors } from '../../theme'

export interface Props {
  color?: keyof Colors
  cakePriceUsd?: number
}

const PriceLink = styled.a`
  display: flex;
  align-items: center;
  svg {
    transition: transform 0.3s;
  }
  :hover {
    svg {
      transform: scale(1.2);
    }
  }
`

const CakePrice: React.FC<Props> = ({ cakePriceUsd, color = 'textSubtle' }) => {
  return cakePriceUsd ? (
    <PriceLink
      href="https://app.cronaswap.org/swap?outputCurrency=0x7AC0bCFC21e2bd0C42f3CF0996518e10747EAE44"
      target="_blank"
    >
      <img src={PancakeRoundIcon} alt="Mariachi Logo" width="24px" style={{ marginRight: '8px' }} />
      <Text color={color} bold>{`$${cakePriceUsd.toFixed(3)}`}</Text>
    </PriceLink>
  ) : (
    <Skeleton width={80} height={24} />
  )
}

export default React.memo(CakePrice)
