import BigNumber from 'bignumber.js'
import { BIG_TEN } from 'utils/bigNumber'

BigNumber.config({
  EXPONENTIAL_AT: 1000,
  DECIMAL_PLACES: 80,
})

export const BSC_BLOCK_TIME = 5.8

export const BASE_BSC_SCAN_URLS = {
  25: 'https://cronos.crypto.org/explorer/',
}

