export const CAKE_PER_YEAR = CAKE_PER_BLOCK * BLOCKS_PER_YEAR
export const BASE_URL = 'https://mariachi.finance'
export const BASE_ADD_LIQUIDITY_URL = `${BASE_URL}/add`
