import BigNumber from 'bignumber.js'
import { useEffect } from 'react'
import { useSelector } from 'react-redux'
import useRefresh from 'hooks/useRefresh'
import { useAppDispatch } from 'state'
import { fetchPoolsPublicDataAsync, fetchPoolsUserDataAsync } from './actions'
import { State, Farm, Pool } from './types'
import { QuoteToken } from '../config/constants/types'
import { fetchFarmsPublicDataAsync } from './farms'

const ZERO = new BigNumber(0)
const BIG_TEN = new BigNumber(10)

export const getBalanceAmount = (amount: BigNumber, decimals = 18) => {
  return new BigNumber(amount).dividedBy(BIG_TEN.pow(decimals))
}

export const useFetchPublicData = () => {
  const dispatch = useAppDispatch()
  const { slowRefresh } = useRefresh()
  useEffect(() => {
    dispatch(fetchFarmsPublicDataAsync())
    dispatch(fetchPoolsPublicDataAsync())
  }, [dispatch, slowRefresh])
}

// Farms

export const useFarms = (): Farm[] => {
  const farms = useSelector((state: State) => state.farms.data)
  return farms
}

export const useFarmFromPid = (pid): Farm => {
  const farm = useSelector((state: State) => state.farms.data.find((f) => f.pid === pid))
  return farm
}

export const useFarmFromSymbol = (lpSymbol: string): Farm => {
  const farm = useSelector((state: State) => state.farms.data.find((f) => f.lpSymbol === lpSymbol))
  return farm
}

export const useFarmUser = (pid) => {
  const farm = useFarmFromPid(pid)
  return {
    allowance: farm.userData ? new BigNumber(farm.userData.allowance) : new BigNumber(0),
    tokenBalance: farm.userData ? new BigNumber(farm.userData.tokenBalance) : new BigNumber(0),
    stakedBalance: farm.userData ? new BigNumber(farm.userData.stakedBalance) : new BigNumber(0),
    earnings: farm.userData ? new BigNumber(farm.userData.earnings) : new BigNumber(0),
  }
}

export const useFarmsManual = (): Farm[] => {
  const farms = useSelector((state: State) => state.farms.data)
  return farms
}
// Pools

export const usePools = (account): Pool[] => {
  const { fastRefresh } = useRefresh()
  const dispatch = useAppDispatch()
  useEffect(() => {
    if (account) {
      dispatch(fetchPoolsUserDataAsync(account))
    }
  }, [account, dispatch, fastRefresh])

  const pools = useSelector((state: State) => state.pools.data)
  return pools
}

export const usePoolFromPid = (sousId): Pool => {
  const pool = useSelector((state: State) => state.pools.data.find((p) => p.sousId === sousId))
  return pool
}

export const usePriceCroUSDC = (): BigNumber => {
  const pid = 999
  const farm = useFarmFromPid(pid)
  return farm.tokenPriceVsQuote ? new BigNumber(farm.tokenPriceVsQuote) : ZERO
}

export const usePriceMariachiCro = (): BigNumber => {
  const pid = 1 // MARIACHI
  const farm = useFarmFromPid(pid)
  const croPrice = usePriceCroUSDC()
  const sum = farm.tokenPriceVsQuote ? new BigNumber(farm.tokenPriceVsQuote).times(croPrice) : ZERO
  return new BigNumber(sum) || ZERO
}

export const useBusdPriceFromPid = (pid: number): BigNumber => {
  const farm = useFarmFromPid(pid)
  return farm && new BigNumber(farm.tokenPriceBusd)
}

export const useLpTokenPrice = (symbol: string) => {
  const mariachiPrice = usePriceMariachiCro()
  const farm = useFarmFromSymbol(symbol)
  const farmTokenPriceInUsd = useBusdPriceFromPid(farm.pid)
  if (symbol === QuoteToken.MARIACHI) {
    return mariachiPrice
  }
  const totalSupply = new BigNumber(farm.lpTotalSupply)
  const totalInQuoteToken = new BigNumber(farm.lpTotalInQuoteToken)
  let lpTokenPrice = ZERO

  if (totalSupply.gt(0) && totalInQuoteToken.gt(0)) {
    // Total value of base token in LP
    const valueOfBaseTokenInFarm = farmTokenPriceInUsd.times(farm.tokenAmountInLP)
    // Double it to get overall value in LP
    const overallValueOfAllTokensInFarm = valueOfBaseTokenInFarm.times(2)
    // Divide total value of all tokens, by the number of LP tokens
    const totalLpTokens = getBalanceAmount(totalSupply)
    lpTokenPrice = overallValueOfAllTokensInFarm.div(totalLpTokens)
  }

  return lpTokenPrice
}

export const useTotalValue = (): BigNumber => {
  const farms = useFarms()
  const bnbPrice = usePriceCroUSDC()
  const mariachiPrice = usePriceMariachiCro()
  let value = new BigNumber(0)

  for (let i = 0; i < farms.length; i++) {
    const farm = farms[i]

    if (farm.pid !== 999 && farm.lpTotalInQuoteToken) {
      let val
      if (farm.isTokenOnly) {
        val = new BigNumber(farm.tokenPriceVsQuote).times(farm.tokenAmount).div(100)
      } else if (farm.quoteTokenSymbol === QuoteToken.CRO) {
        val = bnbPrice.times(farm.lpTotalInQuoteToken)
      } else if (farm.quoteTokenSymbol === QuoteToken.MARIACHI) {
        val = mariachiPrice.times(farm.lpTotalInQuoteToken)
      } else {
        val = new BigNumber(Number(farm.lpTotalInQuoteToken) / 100)
      }

      value = value.plus(val * 100)
    }
  }

  return value
}
